h1 {
    margin: 15px 0;
    font-size: 2rem;
}

p {
    margin: 15px 0;
}

code:not(pre code) {            /* Code that is not inside a code block */
    padding: 0.2rem 0.4rem;
    background-color: var(--gray);
    border-radius: 3px;
}

blockquote {
    margin-left: 50px;
    padding: 3px 3px 3px 30px;
    background-color: var(--gray);
    border-left: 3px solid black;
    color: var(--black);
}

pre {
    margin: 30px 0;
    padding: 1.5rem 1rem;
    background-color: black;
    color: var(--green);
    border-radius: 8px;
}

a {
    font-weight: bold;
    color: var(--honey);
}

ol, ul {
    margin: 30px 0px 30px 50px;
}

img {
    width: 100%;
    border-radius: 8px;
}