main {
    width: 1000px;
    padding: 4rem 3rem;
    background-color: white;
    border-radius: 8px;
}

.section-title {
    margin-bottom: 30px;
    border-bottom: 4px solid black;
    text-align: center;
    color: var(--honey);
    cursor: default;
}

#editor {
    display: block;
    width: 70%;
    height: 230px;
    margin: 0 auto;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    background-color: black;
    color: var(--green);
    caret-color: var(--green);
    overflow-x: hidden;
    overflow-y: scroll;
    resize: none;
}

#editor::-webkit-scrollbar {
    width: 15px;
}

#editor::-webkit-scrollbar-thumb {
    background: var(--green);
}

#editor:focus {
    outline: none;
}

.reset-button {
    display: block;
    margin: 30px auto 0 auto;
    padding: 0.5rem 4rem;
    font-family: inherit;
    font-size: 1.2rem;
    font-weight: bold;
    letter-spacing: 5px;
    background-color: var(--honey);
    border: 3px solid var(--honey);
    border-radius: 8px;
    cursor: pointer;
    transition: 
        background-color 0.3s ease-in;
}

.reset-button:hover {
    background-color: transparent;
}

#preview-container {
    padding: 1.5rem 3rem;
    border: 3px solid black;
    background-color: hsl(0, 0%, 85%);
    border-radius: 8px;
}

@media (max-width: 1000px) {
    html {
        font-size: 12px;
    }

    main {
        width: 500px;
    }

    #editor {
        height: 115px;
    }
}