* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    --honey: hsl(38, 95%, 47%);
    --lightblack: hsl(0, 0%, 27%);
    --green: hsl(120, 73%, 75%);
    --grey: hsl(0, 0%, 80%);
}

body {
    display: flex;
    justify-content: center;
    padding: 3rem 0;
    background-color: var(--lightblack);
    font-family: "Poppins";
    color: black;
}